export function row(content, styles = '') {
    return `<div class="row" style="${styles}">${content}</div>`
}

export function col(content) {
    return `<div class="col-sm">${content}</div>`
}

export function css(styles = {}) {
    //const keys = Object.keys(styles)
    //const array = keys.map(key => {
    //    return `${key}: ${styles[key]}`
    //})
    //return array.join(';')
    if (typeof styles === 'string') return styles
    const toString = key => `${key}: ${styles[key]}`
    return Object.keys(styles).map(toString).join(';')
}

export function block(type) {
    return `
      <form name="${type}">
        <h5>${type}</h5>
        <div class="form-group">
          <input class="form-control form-control-sm mb-2" name="value" placeholder="value">
        </div>
        <div class="form-group">
          <input class="form-control form-control-sm mb-2" name="styles" placeholder="styles">
        </div>
        <div class="form-group justify-content-md-end">
            <button type="submit" class="btn btn-primary btn-sm">Add element</button>
        </div>
      </form>
      <hr />
    `
}